.dialog{padding-bottom:10px;height:20px;font-size:18px;width:80%;margin:10px auto 10px auto;border-radius:15px;padding:2%}.dialog.alert{color:orange}.dialog.won{color:green}.dialog.lost{color:red}@media only screen and (max-device-width: 480px){.tile.eval{height:14px !important;width:12px !important;max-width:14px !important;max-height:14px !important;background-color:red !important}}.tile{border:0px;border-radius:50%;outline:none;background-color:#e4e4e4;transition:all .1s ease-in-out}.tile.c0{background-color:#847dd3}.tile.c1{background-color:#ef476f}.tile.c2{background-color:#ffd166}.tile.c3{background-color:#06d6a0}.tile.c4{background-color:#118AB2}.tile.c5{background-color:#252323}.tile.c6{background-color:red}.tile.c7{background-color:#000}.tile.board,.tile.guess{height:80%;min-height:32px;width:32px;margin:3px}.tile.eval{height:80%;min-height:16px;width:16px;margin:2px}.tile.color{border-radius:25%;height:40px;width:40px}.tile.color.active{-webkit-transform:scale(1);transform:scale(1)}.tile.color.inactive{opacity:0.5;margin:3px;-webkit-transform:scale(0.7);transform:scale(0.7)}.tile.color.inactive:hover{opacity:1;-webkit-transform:scale(1);transform:scale(1);cursor:pointer}.tile.inactive{opacity:0.4}.tile.guess:hover{cursor:pointer;-webkit-filter:brightness(95%);filter:brightness(95%)}.game-container{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;margin-top:10px}.row.board{height:40px}.row.eval{height:20px}.button{border:0px;margin:6px;font-size:1em;font-family:'Lexend';border-radius:25px;background-color:#e4e4e4;outline:none;padding:10px;text-transform:lowercase}.button.clear-guess{height:20px;width:20px;padding:12px}.button.clear-guess div{position:relative;top:-12px;left:-4px}.button:hover{background-color:#cecece;cursor:pointer}.button:active{background-color:#8d8d8d}.button:disabled{cursor:default;background-color:#e4e4e4}body{font-family:'Lexend'}h1{font-size:2em;color:#3a3a3a}h1{margin-bottom:2px}nav ul{width:50%;margin:auto;padding:0px}nav li{display:inline}nav a{color:black;padding:2px 10px;margin:auto;text-decoration:none}nav a:hover{font-weight:bolder}nav a:active,nav a :visited{font-weight:bolder}nav a.is-active{font-weight:bolder}.help p{text-align:left}.help hr{margin:20px auto 20px auto;background-color:#e9e9e9;height:2px;border:0px}.App{box-sizing:border-box;text-align:center;width:80vw;max-width:400px;height:100vh;margin:auto}

